import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';
import styled from 'styled-components';

import compose from 'recompose/compose';
import withHandlers from 'recompose/withHandlers';
import LinkButton from '../LinkButton';

/** icons */
import FaThumbUp from 'react-icons/lib/fa/thumbs-o-up';
import FaCar from 'react-icons/lib/fa/automobile';
import FaFork from 'react-icons/lib/fa/code-fork';
import FaBasket from 'react-icons/lib/fa/shopping-basket';

import media from '../../media';

const Container = styled.div`
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  width: 220px;
  height: 300px;
  margin: 8px;
  border: 1px solid #eee;
  border-radius: 12px;
  background-color: white;
  padding: 16px 8px;

  ${media.mobile`
    height: inherit;
    width: 90%;
  `}
`

const Icon = styled.i`
  color: orange;
  display: flex;
  justify-content: center;
  align-items: center;

   > svg {
     fill: orange;
   }
`;

const Title = styled.h4`
  margin: 0;
  padding-left: 8px;
`;

const Content = styled.div`
  padding: 24px 8px;
`

const IntroCard = ({ title, type, content, link, renderIcon }) => {
  return (
    <Container>
      <Icon>
        {renderIcon()}
        <Title>{title}</Title>
      </Icon>
      <Content>{content}</Content>
      <LinkButton to={link}>觀看菜單</LinkButton>
    </Container>
  );
}

IntroCard.propTypes = {
  title: PropTypes.string,
  type: PropTypes.oneOf(['like', 'car', 'fork', 'delivery']),
  content: PropTypes.node.isRequired,
  link: PropTypes.string.isRequired,
};

export default compose(
  withHandlers({
    renderIcon: ({ type }) => () => {
      switch (type) {
        case 'like':
          return <FaThumbUp />;
        case 'car':
          return <FaCar />;
        case 'fork':
          return <FaFork />;
        case 'delivery':
          return <FaBasket />;
        default:
          return null;
      }
    }
  })
)(IntroCard);



