import React from 'react';
import styled from 'styled-components';
import compose from 'recompose/compose';
import withProps from 'recompose/withProps';

import Carousel from '../components/Carousel';
import Title from '../components/Title';
import IntroCard from '../components/IntroCard';

const IntroBlocks = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: top;
  padding-top: 32px;
`;

const IndexPage = ({ cards }) => {
  return (
    <div>
      <Carousel />
      <Title
        title="小火鍋，選頂豐。"
        subtitle="平價小火鍋的不二選擇"
      />
      <IntroBlocks>
        {cards.map((card, index) => (
          <IntroCard key={index} {...card} />
        ))}
      </IntroBlocks>
    </div>
  );
}

export default compose(
  withProps({
    cards: [
      {
        type: 'like',
        title: '環境優質',
        link: '/menu/',
        content: '頂級鍋爐+舒適空調，坐在柔軟的座椅上輕鬆享用。頂豐烹飪團隊，美味可口，衛生保證，讓你有歡愉的心情！還不即刻前往。'
      },
      {
        type: 'delivery',
        title: '份量不小',
        link: '/menu/',
        content: '平民美食新霸主，一人一鍋大飽口福。湯頭精心熬煮，搭配美味鍋底只要百元起！'
      },
      {
        type: 'fork',
        title: '一鍋兩吃',
        link: '/menu/',
        content: '除了小火鍋外，上等銅盤烤肉，牛、豬、羊任選兩樣。外加雙葉冰淇淋吃到飽，絕對滿足大食客的胃！'
      },
      {
        type: 'car',
        title: '外送服務',
        link: '/menu/',
        content: '滿五鍋可外送，外送20鍋九折優惠！快來看看菜單'
      },
    ],
  })
)(IndexPage)



